
.midr .midr2 .cell {
  width: 25%;
  height: 260px;
  float: left;
  padding: 20px 20px 0px 0px;
  box-sizing: border-box;
}
.midr .midr2 .cell img {
  width: 100%;
  height: 180px;
}
.midr .midr2 .cell .title {
  width: 95%;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-left: 5px;
}
.midr .midr2 .cell .time {
  width: 100%;
  height: 30px;
  line-height: 30px;
  padding-left: 5px;
  font-size: small;
}
.midr .midr2 .cell .time span a {
  color: #ccc;
  display: inline-block;
  float: right;
  font-size: small;
}


